.hotel-value-container,
.hotel-container {
  display: flex;
  flex-direction: row;
  align-items: center;
  margin-right: 2%;
  justify-content: unset;

  .hotel-name{
    margin-left: 1%;
    min-width: 50%;
  }
  .edit-activity {
    margin-left: 2%;
  }
}

.hotel-value-container{
  .input{
    margin-right: 2%;
    margin-left: 1%;
  }
}

.route-planner{
  .title{
    display: flex;
    justify-content: space-between;
    flow-direction: row;
    .icon{
      cursor: pointer;
    }
    .icon-group{
      display: flex;
      flex-direction: row;
    }
  }
}
.review-route-planner-container,
.route-planner-container{
  overflow: auto;

  .cards {
    width: 100%;
  }
  .day-container{
    width: 100% !important;
    margin: 5% !important;
  }
  .new-activity-button-container {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    // padding: 0 25% !important;

    .new-activity-container {
      align-self: center;
    }
  }
  .save-share-buttons {
    bottom: 2%;
    margin-left: 38%;
  }
  .map-button {
    position: absolute;
    right: 5%;
  }
}
.review-route-planner-container {
  height: 100%;
}
.modal{
  height: 40%;

  .model-content{
    font-size: 2em;
    margin-top: 28%;
    text-align: center;
    color: teal;
    font-weight: bold;
  }
}
